<template>
  <div>
    <div v-for="item in nameList" :key="item.id" class='columns block m-2'>
      <div class="box column is-half is-offset-one-quarter">
        {{ item.ITEM_NAME }}  
        <Badge type="drug"/>
      </div>

    </div>
  </div>
</template>

<script>
import Badge from './Badge.vue'

export default {
  name: "ResultList",
  props: ['resultList'],
  components: {
    Badge,
  },
  computed: {
    nameList: function(){
      let list = []
      for(let id in this.resultList){
        list.push(this.resultList[id])
      }
      return list
    }
  },
  data: function(){
    return {

    }
  }
}
</script>

